<template>
  <div id="vapp">

 <Row>
        <i-col span="24">
          <!-- 顶部菜单 -->

   <Menu mode="horizontal" theme="dark" active-key="1">
   <Row  type="flex" >
        <i-col offset="2"  span="6" style="color:white;font-size:18px"><Icon type="social-vimeo"></Icon>
        {{menus.title}}</i-col>     
        <i-col span="16">
<Row type="flex" justify="end" >
  <i-col :span="menus.navs.length*2">           
          <Menu-item v-for="(v,index) in menus.navs" :key="index"  @click.native="show(v.url)">
            <Icon :type="v.icon"></Icon>
            {{v.title}}
        </Menu-item>        
        </i-col>
</Row>

        </i-col>


      
    </Row>
       
        
        
    </Menu>
          </i-col>       
    </Row>
     <Row type="flex">              
        <i-col  span="2">
<!-- 导航 -->
 <Menu :theme="theme" :active-key="0">
        <MenuGroup v-for="(val) in menus.menus"  :key="val" :title="val.title" >
            <MenuItem v-for="(item,index) in val.items" :key="index" @click.native="show(item.url)" >               
               <Icon :type="item.icon"></Icon>
                {{item.title}}
            </MenuItem>            
        </MenuGroup>
    </Menu>


        </i-col>
        <i-col span="10">
<!-- 内容 -->
 <iframe id="show-iframe" :src="menus.url" frameborder="0">></iframe>

        </i-col>
         <i-col span="2"> </i-col>
    </Row>
  </div>
</template>

<script>
export default {
  name: 'App',
    data () {
            return {    
              theme:"light",
              menus:{"title":"hydra帮助手册","url":"/",
              "navs":[ {"title":"起步"}],
              "menus":[
                {"title":"起步","items":[{"title":"概述","url":"/"},{"title":"hello world"}, {"title":"六大服务器"}, {"title":"启动原理"}]},
                {"title":"构建","items":[{"title":"API服务"},{"title":"WEB服务"},{"title":"WS服务"},{"title":"RPC服务"},{"title":"CRON服务"},{"title":"MQC服务"}]}
                ]}    
            }
        },
    mounted(){
        this.$fetch("/menus.json")
        .then(res => {
            this.menus=res
            document.title=res.title
        })
        .catch(err => {
          console.debug(err)
        });
      this.resetIframe();
  },
  methods:{
    resetIframe:function(){
      const oIframe = document.getElementById('show-iframe');
      const deviceWidth = document.documentElement.clientWidth;
      const deviceHeight = document.documentElement.clientHeight;
      oIframe.style.width = deviceWidth + 'px';
      oIframe.style.height = deviceHeight + 'px';
  
		if (oIframe.attachEvent){
        oIframe.attachEvent("onload", function(){
        var body = document.getElementById('show-iframe').contentWindow.document.getElementsByClassName("markdown-preview")  
        body[0].style.left="35%"
			});
		} else {
        oIframe.onload = function(){
        var body = document.getElementById('show-iframe').contentWindow.document.getElementsByClassName("markdown-preview")       
        body[0].style.left="35%"
			};
		}
    },
    show:function(url){
      this.menus.url=url
      console.log("len:",url.length)
      console.log("sub:",url.substring(0,4))

      if(url.length > 4 && url.substring(0,4)=="http"){
          window.location=url
          return
      }
      this.resetIframe()
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
